import React from "react";
function AboutMe(props) {
  return (
    <div className="abm-container" id="about-me">
      <div className="abm-container-textual">
        <div className=" section-title">
          <div className="square"></div>
          About Me
        </div>
        <div className="abm-container-peach-line">
          An Enthusiastic Software Developer.
        </div>
        <div className="abm-container-intro">
          Hi, My name is Kajal Babar and I live in Pune, India. I am a Computer
          Engineer. I have developed cross-platform, interactive websites.
          <p>
            I am always excited to explore challenges and like to work in a
            diverse environment. I code beautifully and love what I do.
          </p>
        </div>
        <div className="abm-container-btns">
          <a
            className="resume"
            href="./assets/resume/Kajal_Babar_CV.pdf"
            download
          >
            <img src="./assets/download.svg" alt="Download" />
            Download CV
            {/* <a href="#contact">Let's Talk</a> */}
          </a>
        </div>
      </div>
      <div className="abm-container-photo">
        <img src="./assets/photo.jpeg" alt="" />
      </div>
    </div>
  );
}

export default AboutMe;
