import React, { Component } from "react";
import { InView } from "react-intersection-observer";

class Skills extends Component {
  skills = [
    { name: "HTML", image: "/assets/skills/html.svg", className: "html" },
    { name: "C++", image: "/assets/skills/cpp.svg", className: "cpp" },
    { name: "CSS", image: "/assets/skills/css.svg", className: "css" },
    { name: "C", image: "/assets/skills/c.svg", className: "c-skill" },
    { name: "Javascript", image: "/assets/skills/js.svg", className: "js" },
    { name: "VsCode", image: "/assets/skills/vscode.svg", className: "vscode" },
    { name: "React", image: "/assets/skills/react.svg", className: "react" },
    { name: "Java", image: "/assets/skills/java.svg", className: "java" },
    { name: "Git", image: "/assets/skills/git.svg", className: "git" },
  ];

  state = {
    isInView: false,
  };

  handleInView = (inView) => {
    if (inView) {
      this.setState({
        isInView: true,
      });
    }
  };

  render() {
    return (
      <InView
        as="div"
        threshold={0.8}
        onChange={(inView, entry) => this.handleInView(inView, entry)}
      >
        <div className="skills" id="skills">
          <div className="skills-container">
            <div className="section-title">
              <div className="square"></div>Talks about
            </div>
            {/* <div className="title-description">
              I talk about about this bubbles
            </div> */}
            <div className="skills-content">
              {this.skills.map((val) => (
                <div
                  className={`skills-content-name ${
                    this.state.isInView && val.className
                  }`}
                  key={val.name}
                >
                  <img src={val.image} alt={val.name} />
                  {val.name}
                </div>
              ))}
            </div>

            {/* BUBBLES CONTAINER */}
            <img
              src="./assets/skills/bubble1.svg"
              alt=""
              className="bubble bubble1"
            />
            <img
              src="./assets/skills/bubble2.svg"
              alt=""
              className="bubble bubble2"
            />
            <img
              src="./assets/skills/bubble3.svg"
              alt=""
              className="bubble bubble3"
            />
            <img
              src="./assets/skills/bubble2.svg"
              alt=""
              className="bubble bubble4"
            />
            <img
              src="./assets/skills/bubble3.svg"
              alt=""
              className="bubble bubble5"
            />
            <img
              src="./assets/skills/bubble1.svg"
              alt=""
              className="bubble bubble6"
            />
          </div>
        </div>
      </InView>
    );
  }
}

export default Skills;
