import React, { useContext, useEffect, useState } from "react";
import { ThemeContext } from "./Common/navbarTheme";

function Navbar(props) {
  const [pageNumber, setPageNumber] = useState(0);
  const navBarTheme = useContext(ThemeContext);
  const [isMenuOpen, setMenuFlag] = useState(false);
  // eslint-disable-next-line
  useEffect(() => {
    // const page = localStorage.getItem("pageNum");
    // setPageNumber(Number(page));

    //MENU BUTTON HANDLING
    const menuBtn = document.querySelector(".menu-btn");
    menuBtn.addEventListener("click", menuHandler);
    return () => {
      const menuBtn = document.querySelector(".menu-btn");
      menuBtn?.removeEventListener("click", menuHandler);
    };
  });

  const setTabNumber = (number, id) => {
    localStorage.setItem("pageNum", number);
    // localStorage.setItem("pageName", id);
    setPageNumber(number);
    menuHandler();
  };

  const menuHandler = () => {
    const menuBtn = document.querySelector(".menu-btn");

    isMenuOpen
      ? menuBtn.classList.remove("open")
      : menuBtn.classList.add("open");
    setMenuFlag(!isMenuOpen);
  };

  return (
    <nav
      className={`navbar__container`}
      style={{ background: navBarTheme.theme }}
    >
      <div className={`navbar__container-rs${isMenuOpen ? "-open" : ""}`}>
        <a
          href="#home"
          className={`home${pageNumber === 1 && " current-page"}`}
          onClick={(e) => setTabNumber(1, e.target.id)}
        >
          Home
        </a>
        <a
          href="#about-me"
          className={`home${pageNumber === 2 && " current-page"}`}
          onClick={(e) => setTabNumber(2, e.target.id)}
        >
          About
        </a>
        <a
          href="#projects-section"
          className={`home${pageNumber === 3 && " current-page"}`}
          onClick={(e) => setTabNumber(3, e.target.id)}
        >
          Projects
        </a>
        <a
          href="#skills"
          className={`home${pageNumber === 4 && " current-page"}`}
          onClick={(e) => setTabNumber(4, e.target.id)}
        >
          Skills
        </a>
        <a
          href="#contact"
          className={`home${pageNumber === 5 && " current-page"}`}
          onClick={(e) => setTabNumber(5, e.target.id)}
        >
          Contact
        </a>
      </div>
      <div className="menu-btn">
        <div className="menu-btn__burger"></div>
      </div>
    </nav>
  );
}

export default Navbar;
