import React, { useEffect, useState } from "react";
import { InView } from "react-intersection-observer";
import { ThemeConsumer } from "./Common/navbarTheme";

function ContactDetails(props) {
  const [greet, setGreet] = useState("Good Morning");

  useEffect(() => {
    const hour = new Date().getHours();
    const welcomeTypes = [
      "Good morning",
      "Good afternoon",
      "Good evening",
      "Good night",
    ];
    let welcomeText = "";

    if (hour >= 4 && hour < 12) welcomeText = welcomeTypes[0];
    else if (hour >= 12 && hour < 18) welcomeText = welcomeTypes[1];
    else if (hour >= 18 && hour <= 20) welcomeText = welcomeTypes[2];
    else if (hour > 20 || hour < 4) welcomeText = welcomeTypes[3];
    else welcomeText = "Have a great day!";
    setGreet(welcomeText);
  }, []);
  //THEME CONSUMER
  return (
    <ThemeConsumer>
      {({ setThemeValue }) => (
        <InView
          as="div"
          threshold={0.86}
          onChange={(inView, entry) => {
            if (inView) setThemeValue("#0b2838");
            else setThemeValue("#131416");
          }}
        >
          <div className="contact">
            <svg
              preserveAspectRatio="none"
              viewBox="0 0 100 102"
              height="75"
              width="100vw"
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              className="svgcolor-light"
              id="contact"
            >
              <path
                d="M0 0 L48 100 L100 0 Z"
                fill="#131416"
                stroke="#131416"
              ></path>
            </svg>

            <div className="contact-container">
              {/* <div className=" section-title">
                <div className="square"></div>
                Contact
              </div> */}
              <div className="contact-container-wrapper">
                <div className="contact-container-title">
                  Let's build something great together
                </div>
                <hr />
                <div className="contact-container-content ">
                  <div className="contact-container-content-details">
                    <img src="./assets/email.svg" alt="Email" />{" "}
                    kajal.s.babar@gmail.com
                  </div>
                  <a
                    className="contact-container-content-details"
                    href="https://github.com/babarkajal"
                    target="_blank"
                    rel="noreferrer"
                  >
                    <img src="./assets/github.svg" alt="Github" />{" "}
                    https://github.com/babarkajal
                  </a>
                  <a
                    className="contact-container-content-details"
                    href="https://www.linkedin.com/in/kajal-babar/"
                    target="_blank"
                    rel="noreferrer"
                  >
                    <img src="./assets/linkedin.svg" alt="Linkedin" />{" "}
                    https://www.linkedin.com/in/kajal-babar/
                  </a>
                </div>
              </div>
              <div className="gratitude">Thanks for visiting. {greet}!</div>
            </div>
          </div>
        </InView>
      )}
    </ThemeConsumer>
  );
}

export default ContactDetails;
